@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
body {
    background: #dde1ee;
    font-family: "Source Sans Pro", sans-serif;
    margin: 0;
}
.page-content {
    min-height: calc(100vh - 100px);
}
.wrapper {
    max-width: 800px;
    margin: 3rem auto;
    background: white;
    border-radius: 10px;
    padding: 1rem;
}
.acceptable-formats {
    font-size: 14px;
    color: gray;
}
@media (max-width: 576px) {
    .wrapper {
        margin: 1rem auto;
    }
}
.form {
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
    background: #f7f8fb;
    border: 1px solid #e4e4e4;
    border-radius: 10px;
}
.form input {
    border: none;
    min-width: 150px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    outline: none;
    padding: 10px;
    font-size: 1rem;
    background: #e6e6e6;
    font-family: "Source Sans Pro", sans-serif;
}
.form button {
    border: none;
    padding: 10px 20px;
    background: #13111b;
    color: lightgray;
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: "Source Sans Pro", sans-serif;
}
.error {
    color: #cc2727;
    font-size: 14px;
    margin-left: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.colors-container {
    margin-top: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.color-tile {
    padding: 10px;
    width: 30%;
    height: 75px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    transition: all 0.3s ease;
}
.color-tile:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

@media (max-width: 576px) {
    .color-tile {
        width: 100%;
    }
}
.color-copied {
    font-size: 14px;
    color: white;
}

footer {
    text-align: center;
    color: gray;
}
footer a {
    color: black;
    text-decoration: none;
}

